import { findIndex, uniqBy } from "lodash";


const state = () => ({
    paidBooks: [],
    cartBooks: [],
    likeBooks: [], 
    deletePaidBooks: [],
    deleteCartBooks: [],
    deleteLikeBooks: [],
});


const getters = {
    likeBooksAfterUniq(state) {
        return uniqBy(state.likeBooks, "id");
    },
    cartBooksAfterUniq(state) {
        return uniqBy(state.cartBooks, "id");
    },
    paidBooksAfterUniq(state) {
        return uniqBy(state.paidBooks, "id");
    },
};


const actions = {

};


const mutations = {
    addToLike(state, payload) {
        if(payload !== undefined || payload !== null) {
            return state.likeBooks.push(payload), true;
        }
        else {
            return new Error("item books can't be undefined or null!");
        }
    },
    addToPaid(state, payload) {
        if(payload !== undefined || payload !== null) {
            return state.paidBooks.push(payload), true;
        }
        else {
            return new Error("item books can't be undefined or null!"), false;
        }
    },
    addToCart(state, payload) {
        if(payload !== undefined || payload !== null) {
            return state.cartBooks.push(payload), true;
        }
        else {
            return new Error("item books can't be undefined or null!"), false;
        }
    },
    removeLikeBook(state, payload) {
        let findIndexRemoveItem = findIndex(state.likeBook, (book) => { return book.title === payload.title && book.author === payload.author });
        

        return state.likeBooks.splice(findIndexRemoveItem, 1), state.deleteLikeBooks.push(payload);
    },
    removePaidBook(state, payload) {
        let findIndexRemoveItem = findIndex(state.paidBook, (book) => { return book.title === payload.title && book.author === payload.author });
        

        return state.paidBooks.splice(findIndexRemoveItem, 1);
    },
    removeCartBook(state, payload) {
        let findIndexRemoveItem = findIndex(state.cartBook, (book) => { return book.title === payload.title && book.author === payload.author });
        

        return state.cartBooks.splice(findIndexRemoveItem, 1);
    },
};


export default {
    namespaced: true,
    state,
    getters,
    actions,
    mutations
}